import {lazy} from "react";
import RouteProps from "../../common/RoutesProps";

const LinhaCadastro = lazy(() => import('./LinhaCadastro'));
const LinhaEdita = lazy(() => import('./LinhaEdita'));
const LinhaListagem = lazy(() => import('./LinhaListagem'));

const routes: RouteProps[] = [
  {path: '/linhas', exact: true, name: 'Linhas', component: LinhaListagem},
  {path: '/linhas/cadastro', exact: true, name: 'Cadastro de Linha', component: LinhaCadastro},
  {path: '/linhas/:id/editar', exact: true, name: 'Edição de Linha', component: LinhaEdita}
];


export default routes;
