import {lazy} from "react";
import RouteProps from "../../common/RoutesProps";

const EmpresaListagem = lazy(() => import('./Listagem/index'));
const EmpresaEditar = lazy(() => import('./Editar/index'));
const EmpresaCadastrar = lazy(() => import('./Cadastrar/index'));
const RoutesNamed = {
  empresas: {
    listagem: '/empresas',
    editar: '/empresas/editar/:id',
    cadastrar: '/empresas/cadastrar',
  },
};


const routes: RouteProps[] = [
  {path: RoutesNamed.empresas.listagem, exact: true, name: 'Empresas', component: EmpresaListagem},
  {path: RoutesNamed.empresas.cadastrar, exact: true, name: 'Cadastrar Empresa', component: EmpresaCadastrar},
  {path: RoutesNamed.empresas.editar, name: 'Editar Empresa', component: EmpresaEditar},
];


export default routes;
